/*  src/Title.css  */
.Title {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction:column;
    justify-content: baseline;
    align-items: center;
    padding: 1em;
    margin-bottom: 2em;
    background-color: rgb(192, 45, 26);
    color: #fff;
  }
  .Title-Subtitle {
      color: #dfdfdf;
  }
  .Title .nav-link {
    display: inline-block;
    padding: 0.5em 1em;
    color: rgba(255, 255, 255, 0.835);
    font-weight: bold;
    text-decoration: none;
  }
  .Title .nav-link-active {
    color: #fff;
    border-bottom: 2px solid;
  }
  .Title .nav-link:hover {
      color: rgba(200, 200, 200, .75);
  }